.belovedClient {
  padding: 95px 0 128px;
  color: #ffffff;
  background: #5a98d0;
  border-radius: 200px 0 0 0;
}

.belovedClient__inner {
  margin-bottom: 80px;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
}

.belovedClient__title {
  max-width: 240px;
  margin-right: 80px;
}

.belovedClient__short-info {
  width: 540px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.05em;
}

.belovedClient a {
  color: #ffffff;
  text-decoration: none;
  background: transparent;
}

.belovedClient a:hover {
  color: #ffffff;
  text-decoration: none;
  border-color: #ffffff;
  background: rgba(31, 63, 104, 0.2);
}

.belovedClient a:focus {
  text-decoration: none;
  outline: none;
  border-color: #ffffff;
}

.belovedClient__inner-clients {
  display: flex;
  flex-flow: row wrap;
}

.belovedClient__item {
  flex: 1 0 25%;
  width: auto;
  border: 1px solid #ffffff;
  font-size: 18px;
  font-weight: 100;
  line-height: 46px;
  letter-spacing: 0.02em;
  padding: 40px 10px;
  text-align: center;
  border-bottom: none;
  border-right: none;
}

.belovedClient__item:not(:nth-child(8)) {
  text-transform: uppercase;
}

.belovedClient__item:nth-child(-n + 4) {
  border-top: none;
}

.belovedClient__item:first-of-type {
  border-left: none;
}

.belovedClient__item:nth-child(5n) {
  border-left: none;
}

@media only screen and (max-width: 1199px) {
  .belovedClient {
    border-radius: 150px 0 0 0;
  }

  .belovedClient__short-info {
    width: 400px;
  }
}

@media only screen and (max-width: 767px) {
  .belovedClient__item {
    flex: 1 0 50%;
    width: auto;
  }

  .belovedClient__item:nth-child(-n + 4) {
    border-top: 1px solid #ffffff;
  }

  .belovedClient__item:first-of-type {
    border-left: 1px solid #ffffff;
  }

  .belovedClient__item:nth-child(5n) {
    border-left: 1px solid #ffffff;
  }

  .belovedClient__item:nth-child(odd) {
    border-left: none;
  }

  .belovedClient__item:nth-child(-n + 2) {
    border-top: none;
  }
}